import React, { useState } from 'react'
import styled from '@emotion/styled'
import { Flex, Box } from 'reflexbox'
import { Link } from 'gatsby'
import bp from '../../style/breakpoints'
import logo from '../../images/grasshopper-black.svg'
import bang from '../../images/bang.svg'

const Header = styled.header`
  padding: 1rem;
  font-size: 2.5rem;
  margin-bottom: 2rem;
  position: relative;
  font-family: 'Comic Neue', cursive;
  ${props =>
    props.absolute &&
    `
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;`}
`

const Nav = styled.nav`
  ${bp({
    display: ['none', 'block'],
  })}
  margin-left: -300px;
  margin-right: -300px;
  padding-left: 700px;
  margin-top: 40px;
  display: inline-block;
  z-index: 1;
  background: red;
  border: 2px solid black;
  transform: rotate(1.5deg);
  &.expanded {
    display: block !important;
  }
  ul {
    list-style-type: none;
    margin: 0 auto;
  }
  li {
    padding: 1rem;
    ${bp({
      display: ['block', 'inline-block'],
      marginRight: [0, '2rem'],
    })}
    a {
      color: black;
      font-weight: bold;
      text-decoration: none;
    }
  }
`

const MobileButton = styled.button`
  border: 3px solid black;
  position: absolute;
  top: 1rem;
  right: 1rem;
  width: 60px;
  height: 60px;
  padding: 5px;
  background: red;
  ${bp({
    display: ['block', 'none'],
  })}
`

const Bang = styled(Link)`
  background: url(${bang}) no-repeat;
  width: 500px;
  height: 300px;
  padding-top: 100px;
  font-weight: bold;
  padding-left: -25px;
  font-size: 3.5rem;
  text-align: center;
  color: black;
  text-decoration: none;
  display: block;
  position: absolute;
  top: -30px;
  left: -20px;
  transform: rotate(-10deg);
  z-index: 10;
`

export default ({ absolute }) => {
  const [isExpanded, setIsExpanded] = useState(false)
  return (
    <Header absolute={absolute}>
      <Bang to="/">
        <span>grasshopper</span>
      </Bang>
      <Nav className={isExpanded && 'expanded'}>
        <ul>
          <li>
            <Link to="/rules">The rules</Link>
          </li>
          <li>
            <Link to="/players">Players</Link>
          </li>
          <li>
            <Link to="/leagues">Leagues</Link>
          </li>
        </ul>
      </Nav>
      <Flex>
        <Box width={[7 / 9, 1]}></Box>
        <Box width={[2 / 9, 0]} textAlign="right">
          <MobileButton
            type="button"
            onClick={event => {
              event.preventDefault()
              setIsExpanded(!isExpanded)
            }}
          >
            <img src={logo} alt="Grasshopper" />
          </MobileButton>
        </Box>
      </Flex>
    </Header>
  )
}
